import {
  addClassItem,
  removeClassItem
} from './_toggleClassItem';
import throttle from '../vendor/_throttle';

// Референції до елементів
const header = document.querySelector('.header');
const headerBottom = document.querySelector('.header__bottom');
const headerTop = document.querySelector('.header__top');
const body = document.body;

// Налаштування порогу, при якому буде фіксуватися нижня частина хедера
const scrollThreshold = 100;

// Глобальні змінні для відстеження стану
let isHeaderFixed = false;
let headerBottomHeight = 0;

// Вимірювання висоти нижньої частини хедера
const measureHeaderComponents = () => {
  headerBottomHeight = headerBottom.offsetHeight;
  document.documentElement.style.setProperty('--header-bottom-height', `${headerBottomHeight}px`);
};

// Функція для обробки прокручування
const handleHeaderOnScroll = () => {
  const scrollY = window.scrollY;

  if (scrollY > scrollThreshold) {
    if (!isHeaderFixed) {
      addClassItem('.header', 'scroll');
      removeClassItem('.header__top', 'show'); // знімаємо клас анімації, якщо був
      addClassItem('.header__top', 'hide'); // ховаємо верхню частину
      isHeaderFixed = true;
    }
  } else {
    if (isHeaderFixed) {
      removeClassItem('.header', 'scroll');
      removeClassItem('.header__top', 'hide'); // показуємо верхню частину
      addClassItem('.header__top', 'show'); // додаємо анімацію появи
      isHeaderFixed = false;
    }
  }
};

// Після завершення анімації знімаємо клас show
headerTop.addEventListener('animationend', () => {
  headerTop.classList.remove('show');
});

// Оптимізований обробник прокручування
const throttledHandleScroll = throttle(handleHeaderOnScroll, 100);

// Ініціалізація при завантаженні
const initialize = () => {
  measureHeaderComponents();
  handleHeaderOnScroll();
  header.classList.add('hide'); // для плавної появи хедера при завантаженні
};

// Слухачі подій
window.addEventListener('DOMContentLoaded', initialize);
window.addEventListener('resize', throttle(measureHeaderComponents, 250));
window.addEventListener('scroll', throttledHandleScroll);

// Повторний виклик ініціалізації, якщо DOM вже готовий
if (document.readyState === 'complete' || document.readyState === 'interactive') {
  setTimeout(initialize, 1);
}



